.cardProyect{
    /* background:var(--hc);
    color: var(--bodyBG); */
    background: var(--bodyBG);
    /* background:#3f484b; */
    color: #fff;
    height: 500px;
    width: 450px;
    border: 1px solid black;
    border-radius:4px;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: 100%;
    transition: all 0.7s ease-out;
}

.cardProyect__detalle{
    position: relative;
    top: 310px;
    padding: 10px 20px;
    height:200px;
}

.cardProyect__detalle__titulo{
    font-size:26px;
    margin:10px 0
}
.cardProyect__detalle__description{
    margin: 0 0 25px 0;
}
.cardProyect__a{
    background: var(--dgbg);
    font-weight: 600;
    padding: 14px;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
}
.cardProyect__a:hover{
    background: #fff;
    color: var(--bodyBG);
}
.cardProyect:hover{
    /* background:var(--bgHover);
    color: #fff;  */
  /*   color: var(--bodyBG); */
    background: var(--bgHover);
    background-repeat: no-repeat;
    background-size: 100%;
    /* transition: all 0.7s ease-out; */
    transition: 0.5s;
    transform: scale(1.03);
}

